/**
 * @description 
 * @param {*} vm vue
 * @param {*} node 
 * @param {*} checked 
 */
export function setNodeChecked(vm, node, checked) {
  if (!node.disabled) {
    vm.$set(node, "checked", checked);
    vm.$set(node, "indeterminate", false);
  }

  if (node.children && node.children.length > 0) {
    node.children.forEach(cell => {
      setNodeChecked(vm, cell, checked);
    });
  }
}
