<template>
  <div
    class="my-dialog"
    :class="{ 'my-dialog-show': visible }"
    :style="{ 'z-index': zIndex }"
    @click.self="handleClickMask"
  >
    <transition
      name="messagebox-fade"
      @after-enter="$emit('opend')"
      @after-leave="$emit('closed')"
    >
      <div
        class="my-dialog-wrap"
        v-show="visible"
        :style="{ width: width, 'margin-top': top }"
      >
        <!--  -->
        <div class="my-dialog-wrap-title">
          <slot name="title">{{ title }}</slot>
          <i
            class="iconfont icon-close my-dialog-wrap-close-btn"
            v-if="showClose"
            @click="handleClose"
          />
        </div>
        <!--  -->
        <div class="my-dialog-wrap-content" v-if="rendered">
          <slot>
            
          </slot>
        </div>

        <div class="my-dialog-wrap-footer" v-if="$slots.footer">
          <slot name="footer"></slot>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "MyDialog",
  data() {
    return {
      // mask
      zIndex: 2000,
      // 
      rendered: false
    };
  },
  props: {
    // 
    visible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    },
    // ESC
    closeOnPressEscape: {
      type: Boolean,
      default: true
    },
    // 
    width: {
      type: String,
      default: "50%"
    },
    // Dialog CSS  margin-top 
    top: {
      type: String,
      default: "15vh"
    },
    // 
    beforeClose: {
      type: Function
    },
    // body
    appendToBody: {
      type: Boolean,
      default: true
    },
    // 
    showClose: {
      type: Boolean,
      default: true
    }
  },
  watch: {
    visible(newVal) {
      if (newVal) {
        this.rendered = true;
        this.$emit("open");
        this.zIndex = this.$osUI ? this.$osUI.zIndex++ : this.zIndex++;
        if (this.appendToBody) {
          document.body.appendChild(this.$el);
        }
      } else {
        this.$emit("close");
      }
    }
  },
  mounted() {
    window.addEventListener("keydown", this.handleCloseByEsc);
  },
  methods: {
    handleClickMask() {
      this.handleClose();
    },
    /**
     * @description ,
     */
    handleClose() {
      if (typeof this.beforeClose === "function") {
        this.beforeClose(() => {
          this.$emit("update:visible", false);
          this.$emit("close");
        });
      } else {
        this.$emit("update:visible", false);
        this.$emit("close");
      }
    },
    /**
     * @description esc
     */
    handleCloseByEsc(event) {
      if (event.keyCode === 27 && this.closeOnPressEscape && this.visible) {
        this.handleClose();
      }
    }
  },
  beforeDestroy() {
    window.removeEventListener("keydown", this.handleCloseByEsc);
  }
};
</script>
